/**
 * @file utils for book
 * @author Yangholmes 2025-01-24
 */

import { I18nT } from 'vue-i18n';
import cn from 'classnames';

import { t } from '@/i18n';

import { useMessage } from 'naive-ui';

import { genDefaultCover } from './TextCover';
import { Modify } from '@/typeUtils';

/** type of raw book */
export type Book = {
  /** 创建时间 */
  createDate: number;
  /** 更新时间 */
  updateDate: number;
  /** id */
  id: string;
  /** isbn */
  isbn: string;
  /** 10 位 isbn 或 13 位 isbn */
  isbnType: '10' | '13' | 'else';
  /** 出版社 */
  publishers: string[];
  /** 出版日期 */
  publishDate: string,
  /** 出版国家 */
  publishCountry: string,
  /** 出版地 */
  publishPlaces: string[],
  /** 页数 */
  numberOfPages: number,
  /** 页码编排方式 */
  pagination: string,
  /** 语言 */
  languages: string[],
  /** 作者 */
  authors: string[],
  /** 贡献者 */
  contributions: string[];
  /** covers */
  covers: Blob[],
  /** 标题 */
  title: string;
  /** 副标题 */
  subtitle: string;
  /** 版本名称 */
  editionName: string;
  /** 分类 */
  subjects: string[];
  /** 标签 */
  tags: string[];
  /** 简介 */
  summary: string;
  /** 来源 */
  sources: string[];
  /** 评分 */
  rating: number;
}

/** type of book in cell */
export type BookInCell = Book & {
  bookcaseId?: string;
  cellId: string;
}

export type BookInCellRawCover = Modify<BookInCell, { covers: string[]; }>

/**
 * 渲染列表
 * @param list 数组
 * @param placeholder 数组无数据或者长度为空占位符
 */
export function listRender<T extends string>(list?: T[], placeholder?: string, className?: string, placeholderClassName?: string) {
  return list?.length ? list.map(item => <span class={cn(className)}>{item}</span>) : <span class={cn(placeholderClassName)}>{placeholder}</span>;
}

export function scrapeErrorMessage(isbn?: string): Parameters<ReturnType<typeof useMessage>['error']> {
  return [
    () => (<div style={{ textAlign: 'center' }}>
      <I18nT scope="global" keypath="pages.book.utils.askForBookInfo">
        <div><a href={`mailto://bookcase.rb@gmail.com?subject=scrape-isbn&body=[${isbn}]`}>bookcase.rb@gmail.com</a></div>
      </I18nT>
    </div>),
    {
      showIcon: false,
      closable: true,
      duration: 5e3
    }
  ];
}

export function getBookCover(cover?: Blob, bookTitle?: string) {
  if (cover) {
    return URL.createObjectURL(cover);
  }
  else {
    // TODO 随机颜色生成
    return genDefaultCover(200, 300, bookTitle || t('pages.book.utils.defaultCoverTitle'), '#FECB23');
  }
}
